<template>
  <div class="news">
    <div class="news-day news-b">
      <swiper :options="swiperOption">
          <swiper-slide v-for="slide in news" :key="slide.title">
            <img src="http://ooi407n8x.bkt.clouddn.com/anankeji.jpg" alt="">
            <!-- <div class="product-text">
              <p>{{ slide.label }}</p>
              <p>科技之所以叫科技因为我们要用它影响世界</p>
            </div> -->
            </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <div class="news-cp news-b">
      <div class="news-yy">
        <img src="http://ooi407n8x.bkt.clouddn.com/anankeji.jpg" alt="">
      </div>
      <div class="news-yy">
        <img src="http://ooi407n8x.bkt.clouddn.com/anankeji.jpg" alt="">
      </div>
    </div>
    <div class="news-now news-b">
      <h3>科技的魅力</h3>
      <img src="http://ooi407n8x.bkt.clouddn.com/anankeji.jpg" alt="">
      <p>线下课程丨全面构建运营知识体系，涨薪前让自己更有实力</p>
      <span>2018-1-20 9:00-17:30</span>
    </div>
  </div>
</template>

<script>
import { news } from "~/data/news.js";

export default {
  data() {
    return {
      swiperOption: {
        autoplay: 4000,
        setWrapperSize: true,
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true,
        loop: true,
        centeredSlides: true,
        // slidesPerView: "0",
        spaceBetween: 30
      },
      news: news
    };
  },
  components: {}
};
</script>

<style scoped lang="scss">
.news {
  margin: 30px 0;
  width: 100%;
  img {
    width: 100%;
  }
}

.news::after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}

.news-b {
  overflow: hidden;
  float: left;
}

.news-day {
  width: 600px;
  overflow: hidden;
  margin-right: 20px;
}

.news-cp {
  width: 260px;
  overflow: hidden;
  margin-right: 20px;
}

.news-yy {
  width: 281.5px;
  overflow: hidden;
  margin-bottom: 20px;
}

.news-yy:nth-child(2) {
  margin-bottom: 0;
}

.news-now {
  height: 333.33px;
  width: 300px;
  background: #334259;
  color:#fff;
  padding: 15px;
  h3{
    margin-bottom: 10px;
  }
  img{
    margin-bottom: 10px;
  }
  p{
    color:#a6b9d3;
    line-height: 25px;
    margin-bottom: 20px;
  }
  span{
    color:#a6b9d3;
  }
}
</style>



